.Container {
    background-color: #F7F7F7;
    height: 100%;
    border-radius: 25px;
    margin: 0 100px 43px 100px;
}

.logement-list {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 50px 60px;
    padding: 56px 50px;
}

.logement-item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    position: relative;
    max-width: 340px;
    max-height: 340px;
    width: 100%;
    height: 100%;
}

.lien-lgt {
    width: 340px;
    height: 340px;
    border-radius: 10px;
    
}

.logement-item-cover {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    object-fit: cover;
    cursor: pointer;
}

.logement-item p {
    list-style-type: none;
    color: white;
    position: absolute;
    background: linear-gradient(to bottom,
                rgba(255, 255, 255, 0) 0%,
                rgba(0, 0, 0, 0.5) 100%);
    display: flex;
    align-items: flex-end;
    width: 320px;
    height: 320px;
    padding-left: 20px;
    padding-bottom: 20px;
    border-radius: 10px;
    margin: 0;
}

/* /////////////////////////////////////////////////////////////////////////////////////////////// */

@media screen and (max-width: 1440px) {
    .logement-list {
        justify-content: space-between;
    }
}

@media screen and (max-width: 1024px) {
    .Container {
        margin-left: 20px;
        margin-right: 20px;
    }

    .logement-list {
        padding-top: 36px;
        padding-bottom: 36px;
    }
}

@media screen and (max-width: 1024px) {
    .logement-list {
        justify-content: center;
    }
}

@media screen and (max-width: 475px) {
    .logement-list {
        padding: 20px;
    }
}

@media screen and (max-width: 475px) {
    .Container {
        background-color: transparent;
    }

    .logement-list {
        padding: 0;
    }

    .logement-item {
        max-width: 335px;
        max-height: 255px;
    }
    
    .lien-lgt {
        width: 335px;
        height: 255px;
    }
    
    .logement-item p {
        width: 315px;
        height: 235px;
    }
}

@media screen and (max-width: 375px) {
    .Container {
        margin: 0;
        margin-left: 20px;
    }

    .logement-list {
        padding: 0;
        display: flex;
        flex-direction: column;
        grid-gap: 20px;
        gap: 20px;
    }

    
}
